import React, { useEffect, useState } from 'react';
import { Card, Image, Input } from 'semantic-ui-react';
import history from '../../history'
import styles from './home.module.css'
import FastpassImage from '../../assets/images/day20-rocket.png'
import RaffleImage from '../../assets/images/raffle_logo.png'
import MenuImage from '../../assets/images/day82-burger.png'
import SurveryImage from '../../assets/images/120-power-of-pen.png'
import MenuMarketImage from '../../assets/images/day4-polariod.png'
import JukeboxImage from '../../assets/images/113-workstation.png'
import axios from 'axios';

function CustomerHome() {
    const location_id = history.location.search.split('=')[1]
    const [locations, setLocations] = useState()
    useEffect(() => {
        console.log(location_id)
        if(location_id) {
            axios.get(`/api/locations/location_id`, { params: { location_id } }).then(res => {
                setLocations([res.data])
            })    
        }
        else {
            axios.get('/api/locations/all').then(res => {
                setLocations(res.data)
            })
        }
    }, [])
    return (
        <div id="customer-home-container">
            {location_id && locations && <Slider {...locations[0]}></Slider>}
            {!location_id && locations && <Search locations={locations} />}
        </div>
    )
}

const Slider = (props) => {
    console.log(props)
    const icons = {
        'Fastpass': FastpassImage,
        'Raffle': RaffleImage,
        'Survey': SurveryImage,
    }
    return (
        <div className={styles.scroll_box}>
            <div className={styles.scroll_box_wrapper}>
                <div className={styles.scroll_box_container} role="list">
                    <div className={styles.scroll_box__item} role="listitem" key={`scroll-box-item-menu`}>
                        {/* TODO: Add a menu url to the location modal schema and update href to that */}
                        <a target="_blank" href="https://apok0z.webflow.io/">
                            <Card>
                                <Image src={MenuImage} wrapped ui={false} />
                                <Card.Content>
                                    <Card.Header>Menu</Card.Header>
                                    <Card.Meta>
                                        <p style={{ color: 'transparent' }}>
                                            -----
                                        </p>
                                    </Card.Meta>
                                </Card.Content>
                            </Card>
                        </a>
                    </div>
                    <div className={styles.scroll_box__item} role="listitem" key={`scroll-box-item-menumarket`}>
                        {/* TODO: Add a menu url to the location modal schema and update href to that */}
                        <a target="_blank" href="https://apok0z.webflow.io/">
                            <Card>
                                <Image src={MenuMarketImage} wrapped ui={false} />
                                <Card.Content>
                                    <Card.Header>Join our Menu Market Waitlist!</Card.Header>
                                    <Card.Meta>
                                        <p style={{ color: 'transparent' }}>
                                            -----
                                        </p>
                                    </Card.Meta>
                                </Card.Content>
                            </Card>
                        </a>
                    </div>
                    {props.music && <div className={styles.scroll_box__item} role="listitem" key={`scroll-box-item-jukebox`}>
                        {/* TODO: Add a menu url to the location modal schema and update href to that */}
                        <a target="_blank" href="/">
                            <Card>
                                <Image src={JukeboxImage} wrapped ui={false} />
                                <Card.Content>
                                    <Card.Header>Jukebox</Card.Header>
                                    <Card.Meta>Play a Song!</Card.Meta>
                                </Card.Content>
                            </Card>
                        </a>
                    </div>}
                    {props.campaigns.map(campaign => {
                        console.log(campaign)
                        return (
                            <div className={styles.scroll_box__item} role="listitem" key={`scroll-box-item-${campaign._id}`}>
                                <a href="/">
                                    <Card>
                                        <Image src={icons[campaign.details.type]} wrapped ui={false} />
                                        <Card.Content>
                                            <Card.Header>{campaign.title}</Card.Header>
                                            <Card.Meta>{campaign.details.type}</Card.Meta>
                                        </Card.Content>
                                    </Card>
                                </a>
                            </div>
                        )
                    })}
                </div>

            </div>
        </div>
    )
}

const Search = (props) => {
    const [searchTerm, setSearchTerm] = useState('')
    const [locations, setLocations] = useState(props.locations)
    console.log(locations)
    function handleChange(e) {
        setSearchTerm(e.target.value)
        if (e.target.value === '') {
            setLocations(props.locations)
        }
        else {
            const filteredLocations = props.locations.filter(location => location.name.toLowerCase().includes(e.target.value.toLowerCase()))
            setLocations(filteredLocations)
        }
    }
    function handleClick(location) {
        history.push(`/customer-home/?location_id=${location._id}`)
    }
    return (
        <div id="search-container" className={styles.search_container}>
            <input placeholder='Search' style={{ borderColor: 'grey', height: '30px', width: "95%", marginLeft: 'auto', marginRight: 'auto' }} value={searchTerm} onChange={handleChange} />
            {locations.map(location => {
                return (
                    <div key={location._id} style={{ borderBottom: '.5px solid grey' }}>
                        <div id="search-item-container" stylele={{ width: "95%", margin: 'auto', cursur: 'pointer' }} onClick={handleClick.bind(null, location)}>
                            <h1>{location.name}</h1>
                        </div>
                    </div>
                )
            }
            )}
        </div>
    )
}

export default CustomerHome